<script>
import constants from "../../util/constants";
import { mapState } from "vuex";

export default {
  name: "signup",
  data() {
    return {
      is_doing_action: false,
      register_form: {
        nom: "",
        prenom: "",
        sexe: "m",
        email: "",
        password: "",
        conf_password: "",
        service_id : 5,
        type : "User",
        status : "1",
        phone : ""
      },
      action_message: "",
    };
  },
  methods: {
    verifierAgent(agent) {
      let message = "";
      if (agent.nom == undefined || agent.nom == "") {
        message = "Veuillez entrer un nom correcte svp";
      } else if (agent.postnom == undefined || agent.postnom == "") {
        message = "Veuillez entrer un postnom correcte svp";
      }  else if (agent.prenom == undefined || agent.prenom == "") {
        message = "Veuillez entrer un prénom correcte svp";
      }  else if (agent.sexe == undefined || agent.sexe == "") {
        message = "Veuillez sélectionner un sexe svp";
      } else if (agent.phone == undefined || agent.phone == "") {
        message = "Veuillez entrer un numéro de téléphone correcte svp";
      } else if (agent.email == undefined || agent.email == "") {
        message = "Veuillez entrer une adresse email correcte svp";
      }  else if (agent.password == undefined || agent.password.length < 5) {
        message = "Veuillez entrer un mot de passe ayant au moins 5 caractères";
      } else if (agent.password!== agent.conf_password) {
        message = "Les deux mots de passe ne concordent pas";
      }  else if (agent.type == undefined || agent.type == "") {
        message = "Veuillez sélectionner un type de compte";
      } 
      //  else if (agent.status == undefined || agent.status=="") {
      //   message = "Veuillez sélectionner un status correcte pour le compte";
      // }   
      
      if (message != "") {
        this.$swal({
          type: "warning",
          title: "Les données fournies sont incorrcetes",
          text: message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      return {
        status: message === "",
        message,
      };
    },
    async doRegister() {
      if (!this.verifierAgent(this.register_form).status) return;

      this.action_message = "Création de votre compte utilisateur en cours";
      this.is_doing_action = true;
      let url = constants.api_url + "/users";
      let obj = {
            ...this.register_form,
            service_id : 5
          }
      delete obj.conf_password
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        let response = await fetch(url, {
          // mode: "cors",
          headers  : {
            "Content-Type" : "application/json"
          },
          // credentials: "include",
          body: JSON.stringify(obj) ,
          method: "post",
        });
        let data = await response.json();
        console.log(data);

        if (data.success) {
          // sessionStorage.setItem("utilisateur_id", data.id_utilisateur);
          // this.utilisateur_id = data.id_utilisateur;
          // this.$swal({
          //   type: "success",
          //   title: "Effectué",
          //   text: "Votre compte a été créé avec succès",
          //   // footer: "<a href>Why do I have this issue?</a>",
          // });

          this.$snotify.success("Votre compte a été créé avec succès", {
            timeout: 1500,
            showProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            
          });

          // this.$store.commit("change_connected", {
          //   ...data.utilisateur,
          //   is_connected: true,
          // });

          if (this.$route.query.next) {
            this.$router.push(this.$route.query.next);
          } else {
            this.$router.push("/");
          }
          return;
        } else {
          // this.$swal({
          //   type: "error",
          //   title: "Une erreur est survenue",
          //   text: "Code d'erreur : " + data.status,
          // });

          this.$snotify.error("Erreur :" + data.message, {
            timeout: 0,
            showProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            
          });
        }
      } catch (e) {
        alert("Erruer : " + e.message);
      }

      this.is_doing_action = false;
    },
  },
  computed: {
    ...mapState([
      "is_connected",
      "nom_utilisateur",
      "id_utilisateur",
      "est_admin",
      "paniers",
    ]),
    prix_total() {
      return this.paniers.reduce(
        (s, article) => article.prix_unitaire * article.quantite + s,
        0
      );
    },
  },
  created() {
    if (this.is_connected) {
      this.$router.push("/");
    }
  },
};
</script>

<template>
  <div>
    <h3 class="mb-3 mt-5 text-center">Lelisa-App</h3>

    <div class="card  mx-3 mx-md-0">
      <div class="card-body">
        <form class="forms-sample mt-4" v-if="!is_doing_action">
          <fieldset :disabled="is_doing_action">
            <div class="row">
              <div class="col-md-6">
                <h4>Inscription</h4>
                <hr />

                <b-form-group label="Prénom :" label-for="prenom">
                  <b-form-input
                    type="text"
                    id="prenom"
                    v-model="register_form.prenom"
                    placeholder="Prénom"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Nom :" label-for="nom">
                  <b-form-input
                    type="text"
                    id="nom"
                    v-model="register_form.nom"
                    placeholder="Nom"
                  ></b-form-input>
                </b-form-group>

                <b-form-group label="Postnom :" label-for="postnom">
                  <b-form-input
                    type="text"
                    id="postnom"
                    v-model="register_form.postnom"
                    placeholder="Post-nom"
                  ></b-form-input>
                </b-form-group>

                <b-form-group label="Votre sexe :" label-for="sexe">
                  <select
                    class="custom-select"
                    id="sexe"
                    v-model="register_form.sexe"
                    placeholder="Sexe"
                  >
                    <option value="Masculin">Masculin</option>
                    <option value="Feminin">Féminin</option>
                  </select>
                </b-form-group>
                <b-form-group label="Adresse :" label-for="phone">
                  <b-form-input
                    type="text"
                    id="phone"
                    v-model="register_form.adresse"
                    placeholder="Adresse"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Adresse email :" label-for="email">
                  <b-form-input
                    type="email"
                    id="email"
                    v-model="register_form.email"
                    placeholder="Email"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Téléphone :" label-for="phone">
                  <b-form-input
                    type="text"
                    id="phone"
                    v-model="register_form.phone"
                    placeholder="Téléphone"
                  ></b-form-input>
                </b-form-group>
                
                <b-form-group label="Mot de passe :" label-for="password">
                  <b-form-input
                    type="password"
                    id="password"
                    v-model="register_form.password"
                    placeholder="Mot de passe"
                  ></b-form-input>
                </b-form-group>
                <b-form-group
                  label="Répétez votre mot de passe :"
                  label-for="conf_password"
                >
                  <b-form-input
                    type="password"
                    id="conf_password"
                    v-model="register_form.conf_password"
                    placeholder="Répétez votre !ot de passe"
                  ></b-form-input>
                </b-form-group>
                <b-button
                  @click="doRegister"
                  type="button"
                  variant="success"
                  class="mr-2 btn-lg"
                  >S'inscrire</b-button
                >
                <p class="mt-4">
                  Vous avez déjà un compte ?
                  <router-link to="/login">Connectez-vous</router-link>
                </p>
              </div>
              <div class="col-md-6 d-none">
                <h5 class="mt-5 mb-3">Informations supplémentaires</h5>

                <b-form-group label="Pays de résidence :" label-for="pays">
                  <b-form-input
                    type="text"
                    id="pays"
                    v-model="register_form.pays"
                    placeholder="Pays"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Province (Etat) :" label-for="province">
                  <b-form-input
                    type="text"
                    id="province"
                    v-model="register_form.province"
                    placeholder="Province / Etat"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Ville :" label-for="ville">
                  <b-form-input
                    type="text"
                    id="ville"
                    v-model="register_form.ville"
                    placeholder="Ville"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Adresse :" label-for="adresse">
                  <b-form-input
                    type="text"
                    id="adresse"
                    v-model="register_form.adresse"
                    placeholder="Adresse (Commune, Quartier, Rue, numéro domicile)"
                  ></b-form-input>
                </b-form-group>

                <b-form-group label="Téléphone :" label-for="telephone">
                  <b-form-input
                    type="tel"
                    id="telephone"
                    v-model="register_form.telephone"
                    placeholder="Votre numéro de téléphone"
                  ></b-form-input>
                </b-form-group>
              </div>
            </div>
          </fieldset>
          <!-- <b-button variant="light">Cancel</b-button> -->
        </form>
        <div class="row" v-else>
          <div class="col-md-12">
            <div class="card">
              <div class="card-body">
                <div class="square-path-loader"></div>
                <p class="text-center mt-4">{{ action_message }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>